import './css/Post.css';
import {Pressable,StyleSheet} from "react-native";
import imgLike from "./img/Heart.png";
function Post(content) {
    return (
        <div className='Post'>
            <div className='PostAuthor'>
                <img alt="" src={content.url} className="PostAuthorPFP"/>
                <div className='PostAuthorInfo'>
                    <div className='PostAuthorName'>{content.Name}</div>
                    <div className='PostTime'>{content.Date}</div>
                </div>
            </div>
            <div className='PostContent'>
                <div className='PostText'>
                    {content.Text}
                </div>
                <div className='PostPhotoParent'>
                    <img alt="" src={content.ContentUrl} className="PostPhoto"/>
                </div>
            </div>
            <div className='feedbackContainer'>
                <Pressable style = {styles.Feedback}>
                    <div className='Like'>
                        <div><img alt='' src={imgLike} className="LikeImg"/></div>
                        <div className='LikeCount'>{content.CountLike}</div>
                    </div>
                </Pressable>
                <Pressable style = {styles.Feedback}>
                    <div className='Like'>
                        <div><img alt='' src={imgLike} className="LikeImg"/></div>
                        <div className='LikeCount'>{content.CountLike}</div>
                    </div>
                </Pressable>
            </div>
        </div>
    );
}
const styles = StyleSheet.create({
    Feedback:{
        paddingBottom:'1.5vh',
        marginTop:'1.5vh',
        paddingLeft:'1.5vh',

    }

});

export default Post;