import './css/Object.css';

function Object(data) {
    return (
        <div className="Object">
            <div className='ObjectTime'>
                {data.time}
            </div>
            <div className='ObjectContainer'>
                <div className='ObjectOne'>
                    {data.object}
                </div>
                <div className='ObjectTwo'>
                    {data.name} - {data.aud} ауд.
                </div>
            </div>
        </div>
    );
}

export default Object;