import './css/SidebarProfile.css';

function SidebarProfile(profile) {
    return (
        <div className="SidebarContainer">
            <div className='ProfilePFPparent'>
                <img alt='' className='ProfilePFP' src={profile.url}/>
            </div>
            <div className='ProfileName'>{profile.text}</div>
        </div>
    );
}
  
export default SidebarProfile;