import * as React from 'react';
import { useState} from 'react';
import {View, ScrollView, StyleSheet, Pressable} from "react-native";
import Left from './img/Left.png';
import Right from './img/Right.png';
import Object from './Object';
import {Monday, Tuesday, Wendsday, Thursday, Friday, Saturday} from './ScheduleList';
const Schedule = () => {
    const [scrollPosState, setScroll] = useState(0);
    const scrollRef = React.useRef(null);
    let scrollLeftChange = function(){
        if(scrollPosState !== 0){
            setScroll(scrollPosState - 1)
        }
        else{
            setScroll(5)
        }
    }
    let scrollRightChange = function(){
        if(scrollPosState !== 5){
            setScroll(scrollPosState + 1)
        }
        else{
            setScroll(0)
        }
    }
    let whatDay = function(){
        if(scrollPosState === 0){
            return Monday;
        }
        if(scrollPosState === 1){
            return Tuesday;
        }
        if(scrollPosState === 2){
            return Wendsday;
        }
        if(scrollPosState === 3){
            return Thursday;
        }
        if(scrollPosState === 4){
            return Friday;
        }
        if(scrollPosState === 5){
            return Saturday;
        }
    }
    let whatDayCalendar = function(){
        if(scrollPosState === 0){
            return 'Понедельник';
        }
        if(scrollPosState === 1){
            return 'Вторник';
        }
        if(scrollPosState === 2){
            return 'Среда';
        }
        if(scrollPosState === 3){
            return 'Четверг';
        }
        if(scrollPosState === 4){
            return 'Пятница';
        }
        if(scrollPosState === 5){
            return 'Суббота';
        }
    }
    let ScheduleDateRender = function(){
        return <View style={styles.storyProfile}><div>{whatDayCalendar()}</div></View>
    }
    //let ScheduleRender = function(){
        //for (let index = 0; 2; index++) {
            //return <Object 
                //time={whatDay()[index].time} 
                //object={whatDay()[index].object}
                //name={whatDay()[index].name}
                //aud={whatDay()[index].aud}
            ///>
        //}
    //}
    return (
        <View>
            <View style={styles.AllContainer}>
                <View>
                    <Pressable style = {styles.storyButton} onPress={() => { scrollLeftChange() } } >
                        <img src={Left} alt="logo" />
                    </Pressable>
                </View>
                <ScrollView 
                    horizontal={true} 
                    showsHorizontalScrollIndicator={false} 
                    style={styles.container}
                    ref={scrollRef}
                >
                    <View style={styles.storyContainer}>
                        {ScheduleDateRender()}
                    </View>
                </ScrollView>
                <View>
                    <Pressable style = {styles.storyButton} onPress={() => { scrollRightChange()}}>
                        <img src={Right} alt="logo" />
                    </Pressable>
                </View> 
            </View>
            <tbody>
                {whatDay().map(function(object, i){
                    return <Object 
                        obj={object} 
                        key={i} 
                        time={whatDay()[i].time} 
                        object={whatDay()[i].object}
                        name={whatDay()[i].name}
                        aud={whatDay()[i].aud}
                    />;
                })}
            </tbody>
        </View>
    );
};

const styles = StyleSheet.create({
    AllContainer:{
        flexDirection:"row",
        width: '100%',
        backgroundColor: '#FFF',
        borderRadius: '16px',
        height: '6vh',
        marginTop: '1.5vh',
        marginBottom:'1.5vh'
    },
    storyContainer:{
        flexDirection: "row",
    },
    storyProfile:{
        width: '35vw',
        height: '6vh',
        marginRight: '1.2vw',
        textAlign: 'center',
        marginTop: '2vh',
    },
    storyButton:{
        height:'1vh',
        marginTop:'2vh',
        paddingHorizontal:'1vw'
    },
    container: {},

});
export default Schedule;