import './css/MenuButton.css';
import {StyleSheet, Pressable} from "react-native";
function MenuButton(btn) {
    return (
      <Pressable style={styles.Button}>
        <div className='BtnImageDiv'><img className='BtnImage' src={btn.url} alt=""/></div>
        <div className='BtnTextDiv'><p className='BtnText'>{btn.text}</p></div>
      </Pressable>
    );
  }
  const styles = StyleSheet.create({
    Button:{
        width: '13.5vw',
        backgroundColor: '#fff',
        borderRadius: '16px',
        height: '6vh',
        marginTop: '1.5vh',
        marginLeft:'1vw',
        textAlign:'left',
        paddingLeft:'5%',
        flexDirection:'row',
    },

});
  export default MenuButton;