import './css/Menu.css';
import {
  NavLink,
  BrowserRouter
} from "react-router-dom";
import imgHome from './img/Home.png';
import imgCalendar from './img/Calendar.png';
import imgCategory from './img/Category.png';
import imgProfile from './img/Profile.png';
import imgSetting from './img/Setting.png';
import MenuButton from './MenuButton';
function Menu() {
    return (
      <BrowserRouter>
        <div className="Menu">
          <NavLink to="/"><MenuButton url = {imgHome} text="Новости"/></NavLink>
          <NavLink to="/Schedule"><MenuButton url = {imgCalendar} text="Расписание"/></NavLink>
          <NavLink to="/Services"><MenuButton url = {imgCategory} text="Сервисы"/></NavLink>
          <NavLink to="/Profile"><MenuButton url = {imgProfile} text="Профиль"/></NavLink>
          <NavLink to="/Settings"><MenuButton url = {imgSetting} text="Настройки"/></NavLink>
          <button className='AddBtn'>Добавить сервис</button>
        </div>
      </BrowserRouter>
    );
  }
  
  export default Menu;