import * as React from 'react';
import {View, ScrollView, StyleSheet, Pressable} from "react-native";
import Left from './img/Left.png';
import Right from './img/Right.png';
const Stories = () => {
    const scrollRef = React.useRef(null);
    let scrollPos = 0;
    let scrollLeft = function(){
        if(scrollPos !== 0){
            scrollPos = scrollPos - 40;
        }
        return scrollPos;
    };
    let scrollRight = function(){
        scrollPos = scrollPos + 40;
        return scrollPos;
    };
    return (
        <View style={styles.AllContainer}>
            <View>
                <Pressable style = {styles.storyButton} onPress={() => { scrollRef.current.scrollTo({ x: scrollLeft(), animated: true }) }} >
                    <img src={Left} alt="logo" />
                </Pressable>
            </View>
            <ScrollView 
                horizontal={true} 
                showsHorizontalScrollIndicator={false} 
                style={styles.container}
                ref={scrollRef}
            >
                <View style={styles.storyContainer}>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                    <View style={styles.storyProfile}/>
                </View>
            </ScrollView>
            <View>
                <Pressable style = {styles.storyButton} onPress={() => { scrollRef.current.scrollTo({ x: scrollRight(), animated: true }) }}>
                    <img src={Right} alt="logo" />
                </Pressable>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    AllContainer:{
        flexDirection:"row",
        width: '100%',
        backgroundColor: '#FFF',
        borderRadius: '16px',
        height: '12vh',
        marginTop: '1.5vh',
        marginBottom:'1.5vh'
    },
    storyContainer:{
        flexDirection: "row",
        marginTop:'2vh'
    },
    storyProfile:{
        width: '7vh',
        height: '7vh',
        borderRadius: '3.5vh',
        borderColor: "black",
        borderWidth: 2,
        marginRight: '1.2vw',
    },
    storyButton:{
        height:'1vh',
        marginTop:'5vh',
        paddingHorizontal:'1vw'
    },
    container: {},

});
export default Stories;