import './css/Sidebar.css';
import SidebarProfile from './SidebarProfile';
import ProfilePFP from './img/ProfilePFP.png';
function Sidebar(category) {
    return (
        <div className="Sidebar">
            <div className='Category'>
                <div className='CategoryName'>УЧП
                    <SidebarProfile url={ProfilePFP} text={'СУНЦ'}/>
                    <SidebarProfile url={ProfilePFP} text={'СУНЦ 2'}/>
                </div>
            </div>
            <div className='Category'>
                <div className='CategoryName'>ППОС
                    <SidebarProfile url={ProfilePFP} text={'СУНЦ'}/>
                    <SidebarProfile url={ProfilePFP} text={'СУНЦ 2'}/>
                </div>
            </div>
        </div>
    );
}
  
export default Sidebar;